<template>
  <div class="radio-wrapper">
    <div class="radio-type" v-for="item in groupList" :key="item.id">
      <p class="tag">{{ item.name }}</p>
      <div class="item-wrapper">
        <div
          class="radio-item"
          v-for="list in item.radioList"
          :key="list.radioId"
        >
          <i class="iconfont icon-bofang"></i>
          <img :src="list.radioImg" alt="" />
          <p>{{ list.radioName }}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { onMounted, reactive, toRefs } from "vue";
export default {
  setup() {
    const state = reactive({
      groupList: {},
    });
    onMounted(() => {
      axios({
        url: "/api/getRadioLists",
      }).then((res) => {
        state.groupList = res.data.response.data.data.groupList;
        console.log(res.data.response.data.data.groupList);
      });
    });
    return {
      ...toRefs(state),
    };
  },
};
</script>

<style lang="less" scoped>
.radio-wrapper {
  width: 1028px;
  margin-left: 20px;
  .radio-type {
    .tag {
      margin-left: 22px;
      font-size: 25px;
    }
    .item-wrapper {
      display: flex;
      flex-wrap: wrap;
      .radio-item {
        cursor: pointer;
        width: 20%;
        margin: 20px;
        box-sizing: border-box;
        position: relative;
        img {
          width: 100%;
          border-radius: 10px;
        }
        img:hover {
          filter: brightness(0.7);
        }
        .icon-bofang {
          position: absolute;
          font-size: 81px;
          left: 31%;
          top: 23%;
          display: none;
          border-radius: 50%;
        }
        .icon-bofang:hover {
          background-color: #5df6a6;
        }
      }
      .radio-item:hover {
        .icon-bofang {
          display: block;
        }
      }
    }
  }
}
</style>